import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

const routes: Routes = [
  {
    path: 'eos',
    loadChildren: () => import('./view/modules/eos.module').then(m => m.EosModule)
  },
  {
    path: 'wax',
    loadChildren: () => import('./view/modules/wax.module').then(m => m.WaxModule)
  },
  {
    path: 'jungle',
    loadChildren: () => import('./view/modules/jungle.module').then(m => m.JungleModule)
  },
  {
    path: '**',
    redirectTo: 'eos'
  }
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy' })],
  exports: [RouterModule]
})
export class AppRoutingModule { }
